:root {
  --timing-function: cubic-bezier(.4, 0, .2, 1);
  --animation-duration: 250ms;
}

.gallery {
  max-width: 1200px;
  flex-wrap: wrap;
  gap: 10px;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.photo-card {
  max-width: 390px;
  border-radius: 0 0 4px 4px;
  flex-direction: column;
  align-items: center;
  display: flex;
  box-shadow: 0 1px 3px rgba(0, 0, 0, .12), 0 1px 1px rgba(0, 0, 0, .14), 0 2px 1px rgba(0, 0, 0, .2);
}

img:hover {
  transform: scale(1.03);
}

img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: transform var(--animation-duration) var(--timing-function);
}

.info {
  padding-top: 5px;
  padding-bottom: 5px;
  padding-left: 30px;
  display: flex;
}

.info-item {
  flex-direction: column;
  align-items: center;
  margin-top: 0;
  margin-bottom: 0;
  margin-right: 28px;
  display: flex;
}

